import { useState, useEffect } from "react";
import { Link, useParams, NavLink, useNavigate } from 'react-router-dom';
import { Breadcrumb, SimpleCard } from "app/components";
import { LoadingButton } from '@mui/lab';
import { Formik, Form, Field, FieldArray } from 'formik';
import * as Yup from 'yup';
import axios from 'axios';
import swal from 'sweetalert';
import {
  Card,
  Divider,
  CardContent,
  CardHeader,
  Box,
  Grid,
  Icon,
  IconButton,
  Button,
  styled,
  TextField,
  useTheme,
  RadioGroup, FormControlLabel, Radio

} from "@mui/material";


const Item = styled("div")(({ theme }) => (
  {
    marginBottom: "5px",
    fontSize: '16px',
    fontWeight: '400',
    color: '#484747'
  }
));
const ItemContent = styled("div")(({ theme }) => (
  {
    marginBottom: "15px",
    fontSize: '16px',
    fontWeight: '600',
  }
));

const Container = styled("div")(({ theme }) => (
  {
    margin: "30px",
    [theme.breakpoints.down("sm")]: { margin: "16px" },
    "& .breadcrumb": {
      marginBottom: "30px",
      [theme.breakpoints.down("sm")]: { marginBottom: "16px" },
    },
  }
));
const ContentBox = styled(Box)(() => ({
  height: '100%',
  padding: '32px',
  position: 'relative',
  // background: 'rgba(0, 0, 0, 0.01)',
}));

// form field validation schema
const validationSchema = Yup.object().shape({
  // status: Yup.string()
  //   .required('User Accton is required!'),
  status: Yup.string().when('status', {
    is: (status) => status === 'pending',
    then: Yup.string().required('Please checked Approved or Rejected action!'),
    otherwise: Yup.string(),
  }),
}, [['status', 'status']]);

const UserProfile = () => {

  const { user_id } = useParams();
  const theme = useTheme();
  const navigate = useNavigate();
  const [profile, setProfile] = useState(false);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    fetchData();
  }, []);

  /**
   * Fetching data from server.
   */
  const fetchData = async () => {
    setLoading(true);
    try {
      await axios.get('/sanctum/csrf-cookie').then(response => {
        axios.post('/api/admin/profile')
          .then(response => {
            if (response.data.response_code === 200) {
              setProfile(response.data.data);
            } else if (response.data.response_code === 401) {
            } else {
            }
            setLoading(false);
          })
          .catch(function (error) {
            setLoading(false);
            console.error(error);
          });
      });
    } catch (e) {
      setLoading(false);
    }
  }

  return (
    <>
      <Container>
        <Box className="breadcrumb">
          <Breadcrumb routeSegments={[{ name: "dashboard", path: "/dashboard" }, { name: "My Profile" }]} />
        </Box>

        <Card title="My Profile">
          <ContentBox>
            <CardHeader
              title="My Profile"
              titleTypographyProps={{
                variant: "h4",
                fontSize: '18px',
                fontWeight: '600',
                // textTransform: 'uppercase',
                color: theme.palette.primary.main
              }}
            />
            <CardContent>
              <Box width="100%" overflow="auto">
                {profile && (
                  <>
                    <Grid container>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Name</Item>
                        <ItemContent>{profile.name}</ItemContent>
                      </Grid>
                      <Grid item sm={6} xs={12} lg={3}>
                        <Item>Email</Item>
                        <ItemContent>{profile.email}</ItemContent>
                      </Grid>
                    </Grid>
                  </>
                )}
              </Box>
            </CardContent>
          </ContentBox>
        </Card>

      </Container>
    </>

  );
};

export default UserProfile;
